<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="confirm" 
      :width="800"
    >
      <div slot="title">订单详细数据</div>
      <div class="modal-body">
        <h3>基本信息</h3>
        <div class="row">
          <div class="columns">
            <span class="label">订单ID:</span>
            <span class="value">{{ form.order_id }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">订单号:</span>
            <span class="value">{{ form.order_number }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">货件号:</span>
            <span class="value">{{ form.posting_number }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">运单号:</span>
            <span class="value">{{ form.tracking_number?form.tracking_number:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">系统状态:</span>
            <span class="value">{{ form.system_status?form.system_status:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">货运状态:</span>
            <span class="value">{{ form.posting_status?form.posting_status:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">物流状态:</span>
            <span class="value">{{ form.logistics_status?form.logistics_status:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">快递名称:</span>
            <span class="value">{{ form.delivery?form.delivery.delivery_name:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">快递服务类型:</span>
            <span class="value">{{ form.delivery?form.delivery.delivery_tpl_provider_type:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">快递服务名称:</span>
            <span class="value">{{ form.delivery?form.delivery.delivery_tpl_provider_name:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">仓库名称:</span>
            <span class="value">{{ form.warehouse_name?form.warehouse_name:"暂无" }}</span>
          </div>
        </div>


        <div class="row">
          <div class="columns">
            <span class="label">开始处理时间:</span>
            <span class="value">{{ in_process_at_time?in_process_at_time:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">必须收取时间:</span>
            <span class="value">{{ shipment_date?shipment_date:"暂无" }}</span>
          </div>
        </div>
        <div class="row">
          <div class="columns">
            <span class="label">审核时间:</span>
            <span class="value">{{ form.approval_time?form.approval_time:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">采购时间:</span>
            <span class="value">{{ form.dispatch_time?form.dispatch_time:"暂无" }}</span>
          </div>
        </div>
        <div class="row">
          <div class="columns">
            <span class="label">发货时间:</span>
            <span class="value">{{ form.shipping_time?form.shipping_time:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">作废时间:</span>
            <span class="value">{{ form.cancel_time?form.cancel_time:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">订单总价:</span>
            <span class="value">{{ form.total_price }}</span>
          </div>
          <div class="columns">
            <span class="label">价格货币:</span>
            <span class="value">{{ form.currency_code }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">店铺名称:</span>
            <span class="value">{{ form.shop?form.shop.name:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">负责人员:</span>
            <span class="value">{{ form.owner?form.owner.name:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">取消原因:</span>
            <span class="value">{{ form.cancel_reason?form.cancel_reason:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">取消类型:</span>
            <span class="value">{{ form.cancellation_type?form.cancellation_type:"暂无" }}</span>
          </div>
        </div>
        
        <h3 style="margin-top: 10px;">产品信息</h3>
        <div v-for="(product, index) in form.products" :key="index" style="display: flex;">
          <span style="margin-right: 8px;">{{ index+1 }}.</span>
          <div>
            <div class="row">
              <div class="columns">
                <img :src="product.primary_image" style="width: 50px; height: 50px;" alt="产品主图" />
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">产品名称:</span>
                <span class="value">{{ product.name }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">offer_id:</span>
                <span class="value">{{ product.offer_id }}</span>
              </div>
              <div class="columns">
                <span class="label">sku:</span>
                <span class="value">{{ product.sku }}</span>
              </div>
              <div class="columns">
                <span class="label">product_id:</span>
                <span class="value">{{ product.product_id }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">产品价格:</span>
                <span class="value">{{ product.price }}</span>
              </div>
              <div class="columns">
                <span class="label">产品数量:</span>
                <span class="value">{{ product.quantity }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">fbo佣金百分比:</span>
                <span class="value">{{ product.fbo_commission_percent }}%</span>
              </div>
              <div class="columns">
                <span class="label">fbo佣金额:</span>
                <span class="value">{{ product.fbo_commission_value }}</span>
              </div>
              <div class="columns">
                <span class="label">fbp佣金百分比:</span>
                <span class="value">{{ product.fbp_commission_percent }}%</span>
              </div>
              <div class="columns">
                <span class="label">fbp佣金额:</span>
                <span class="value">{{ product.fbp_commission_value }}</span>
              </div>
            </div>

            <div class="row">

              <div class="columns">
                <span class="label">fbs佣金百分比:</span>
                <span class="value">{{ product.fbs_commission_percent }}%</span>
              </div>
              <div class="columns">
                <span class="label">fbs佣金额:</span>
                <span class="value">{{ product.fbs_commission_value }}</span>
              </div>
              <div class="columns">
                <span class="label">rfbs佣金百分比:</span>
                <span class="value">{{ product.rfbs_commission_percent }}%</span>
              </div>
              <div class="columns">
                <span class="label">rfbs佣金额:</span>
                <span class="value">{{ product.rfbs_commission_value }}</span>
              </div>
              
            </div>
            
            
          </div>
        </div>
    </div>
    </a-modal>
  </div>
</template>

<script>
import moment from 'moment'

export default {
  name: 'FormModal',
  props: ['visible', 'form'],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
    };
  },
  computed: {
    in_process_at_time(){
      return moment(this.form.in_process_at).format('YYYY-MM-DD HH:mm:ss')
    },
    shipment_date(){
      return moment(this.form.shipment_date).format('YYYY-MM-DD HH:mm:ss')
    }
  },
  methods: {
    confirm() {
      this.$emit('cancel', false);
    },
    cancel() {
      this.$emit('cancel', false);
    },
  },
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns .label{
  margin-right: 5px;
}

.row .columns .value{
  margin-right: 5px;
  color: green;
}
</style>